  .card-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 20px;

    .card {
      padding: 0px 55px;
      margin-bottom: 5rem;
      position: relative;

      img {
        border: 7px solid #a6a6a6;
        border-radius: 5px;
        height: 350px;
        width: 255px;
        object-fit: cover;
        display: block;
        margin: 0 auto;
        transition: all 0.3s linear;
      }
    }

    @media(max-width: 500px) {
      .card {
        padding: 0px !important;
      }
    }

    .movie-title,
    .movie-year {
      margin: 0;
      text-align: center;
    }

    .movie-title {
      font-size: 1.2em !important;
      font-weight: bold;
      white-space: nowrap;
      width: 255px;
      margin: 0 auto;
      overflow: hidden;
      text-overflow: ellipsis;
      margin-top: 5px;
    }

    .movie-year {
      font-size: 0.8em;
      margin-top: 3px;
    }

    .movie-desc {
      position: absolute;
      top: 0;
      height: 350px;
      width: 255px;
      opacity: 0;
      transition: opacity 0.3s linear;
      background: #000000b5;
      margin: 7px;
      cursor: pointer;
      text-align: center;

      &:hover {
        opacity: 1;
      }

      &:hover+img {
        border: 7px solid #87c8ff;
      }

      i {
        font-size: 2.5rem;
        color: #87c8ff;
        margin-top: 2rem;
      }

      .rating {
        font-weight: bold;
        margin-top: 1rem;
        font-size: 1.8rem;
      }

      a {
        font-weight: bold;
        color: #1d272f;
        border: none;
        padding: 8px 15px;
        border-radius: 5px;
        background: #87c8ff;
        cursor: pointer;
        text-decoration: none;
        transition: all 0.5s linear;
        position: absolute;
        bottom: 15%;
        left: 0;
        right: 0;
        margin: 0 25%;
      }
    }

  }

  .no-record-true {
    opacity: 1 !important;
    transition: all 0.5s linear;
    display: block !important;
  }

  .no-record {
    // position: absolute;
    width: 100%;
    text-align: center;
    opacity: 0;
    transition: all 0.5s linear;
    display: none;

    img {
      max-width: 450px;
      width: 100%;
      padding-top: 3vh;
    }

    p {
      font-weight: bold;
    }
  }

  /* Hide HTML5 Up and Down arrows. */
  input[type="number"]::-webkit-outer-spin-button,
  input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  input[type="number"] {
    -moz-appearance: textfield;
  }


  // Mobile breakpoint
  @media only screen and (max-width : 480px) {
    .adult {
      width: 100%;
      align-items: center;
    }
  }

  // Snackbar
  #snackbar {
    visibility: hidden;
    min-width: 250px;
    background-color: #000000;
    color: #fff;
    text-align: center;
    border-radius: 5px;
    padding: 16px;
    position: fixed;
    z-index: 1;
    left: 50%;
    transform: translate(-50%, -50%);
    bottom: 30px;
    font-size: 17px;
  }

  #snackbar.show {
    visibility: visible;
    -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
    animation: fadein 0.5s, fadeout 0.5s 2.5s;
  }

  @-webkit-keyframes fadein {
    from {
      bottom: 0;
      opacity: 0;
    }

    to {
      bottom: 30px;
      opacity: 1;
    }
  }

  @keyframes fadein {
    from {
      bottom: 0;
      opacity: 0;
    }

    to {
      bottom: 30px;
      opacity: 1;
    }
  }

  @-webkit-keyframes fadeout {
    from {
      bottom: 30px;
      opacity: 1;
    }

    to {
      bottom: 0;
      opacity: 0;
    }
  }

  @keyframes fadeout {
    from {
      bottom: 30px;
      opacity: 1;
    }

    to {
      bottom: 0;
      opacity: 0;
    }
  }

  // End Snack bar

@keyframes shadow-pulse
{
  0% {
    box-shadow: 0 0 0 0px rgba(197, 197, 197, 0.35);
  }
  100% {
    box-shadow: 0 0 0 15px rgba(255, 255, 255, 0);
  }
}