/* Loader */
.loaderRoot {
  position: relative;
  width: 100%; }

.showbox {
  position: absolute;
  top: 1rem;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 5%; }

.loader {
  position: relative;
  margin: 0px auto;
  width: 50px; }

.loader:before {
  content: '';
  display: block;
  padding-top: 100%; }

.circular {
  animation: rotate 2s linear infinite;
  height: 100%;
  transform-origin: center center;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto; }

.path {
  stroke-dasharray: 1, 200;
  stroke-dashoffset: 0;
  animation: dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;
  stroke-linecap: round; }

@keyframes rotate {
  100% {
    transform: rotate(360deg); } }

@keyframes dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0; }
  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35; }
  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124; } }

@keyframes color {
  100%,
  0% {
    stroke: #a0f1fd; }
  40% {
    stroke: #00ccff; }
  66% {
    stroke: #42b0fa; }
  80%,
  90% {
    stroke: #a4eeff; } }

/* End loader */
.filter-section {
  display: flex;
  justify-content: center;
  flex-wrap: wrap; }
  .filter-section select,
  .filter-section input[type='number'],
  .filter-section .year-input {
    border: none;
    width: 150px;
    border-radius: 5px;
    padding: 0 0.5rem;
    height: 35px;
    background: #2e4469;
    color: #d8d8d8; }
  .filter-section .year-input::placeholder,
  .filter-section .input-icon,
  .filter-section .year-input {
    color: #d8d8d8;
    cursor: pointer; }
  .filter-section .section-container,
  .filter-section form {
    margin: 0 1rem; }
  .filter-section .section-container {
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem; }
    .filter-section .section-container label {
      margin-bottom: 5px;
      color: #a2a2a2; }
    .filter-section .section-container .year-picker {
      z-index: 99;
      width: auto !important;
      border: none !important;
      background: none !important; }
      .filter-section .section-container .year-picker .picker-panel {
        height: 180px !important; }
  .filter-section .adult label {
    text-align: center; }
  .filter-section .switch {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 30px;
    margin: 0; }
  .filter-section .switch input {
    display: none; }
  .filter-section .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #4f658a;
    transition: 0.4s; }
  .filter-section .slider::before {
    position: absolute;
    content: "";
    height: 21px;
    width: 21px;
    left: 5px;
    bottom: 5px;
    background-color: #41b1e1d4;
    transition: 0.4s; }
  .filter-section input:checked + .slider {
    background-color: #303f58; }
  .filter-section input:checked + .slider::before {
    transform: translateX(18px); }
  .filter-section .slider.round {
    border-radius: 34px; }
  .filter-section .slider.round::before {
    border-radius: 50%; }
