@import url('https://fonts.googleapis.com/css?family=Nunito');

body {
  font-family: 'Nunito', sans-serif;
}

.App-header {
  background: #19212f;
  min-height: 100vh;
  color: white;
  display: flex;
  flex-direction: column;
}

.App-header::before {
  content: "";
  margin-top: 10vh;
  width: 100%;
}

.navbar {
  box-shadow: 0px 3px 20px #00000069;
  height: 10vh;
  background: linear-gradient(180deg, #2d446a, #2f4468);
  z-index: 100;
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  img {
    height: 75%;
    margin: auto 0;
    margin-left: 1rem;
  }

  .now-playing {
    margin: auto 0;
    margin-right: 2rem;
    // display: none;

    .tooltip {
      opacity: 0;
      visibility: hidden;
      background: #00000069;
      color: #bfbfbf;
      padding: 5px 7px;
      text-align: center;
      transition: all 1s linear;
      font-size: 1rem;
      font-family: 'Nunito', sans-serif;
      border-radius: 5px;
      position: absolute;
      width: 13rem;
      right: 0;
      z-index: 9999;
      transform: translate(0rem, -0.9rem);
      margin-right: 1.5rem;
    }

    i {
      cursor: pointer;
      font-size: 1.8rem;
      color: #41b1e1;

      &:hover+.tooltip {
        visibility: visible;
        opacity: 1;
      }
    }
  }


  .adv-search-btn {
    border: none;
    background: transparent;
    font-size: 1rem;
    color: #a9a9a9;
    cursor: pointer;
    margin-left: 1rem;
    margin-right: 8rem;
    padding: 0.5rem 1rem;
    opacity: 0.8;
    transition: all 0.4s linear;

    &:hover {
      opacity: 1;
      border-radius: 7px;
      background: #3170a2;
      color: #fff;
    }
  }

}

.showNP{
  display: block !important;
}