@import 'node_modules/react-modal-video/scss/modal-video.scss';

.movie-container {
    padding-top: 2vh;
    padding-bottom: 4rem;
    width: 100%;

    .m-details {
        display: flex;
        flex-direction: column;
        align-items: center;
        flex-wrap: wrap;

        .synopsis-cast {
            display: flex;
            flex-wrap: wrap;
            flex-direction: row;
            justify-content: center;
            margin-top: 6rem;
            max-width: 80%;
            width: 100%;

            .synopsis {
                flex-basis: 40%;

                p {
                    color: #c5c5c5;
                    line-height: 1.7em;
                }
            }

            .cast-container {
                flex-basis: 60%;
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                h2{
                    width: 100%;
                }
                .cast {
                    flex-basis: 150px;
                    text-align: center;
                    cursor: pointer;

                    img {
                        height: 100px;
                        max-width: 100px;
                        width: 100%;
                        object-fit: cover;
                        border-radius: 50px;
                        display: block;
                        margin: 0 auto;
                        background: #2e4469;
                    }
                }
            }
        }
    }

    .m-container {
        padding: 0;
        margin: 0;
        margin-top: 5rem;
        max-width: 80%;
        width: 100%;

        .card {
            margin-top: 0;
            padding: 0;
            padding-right: 2rem;
            margin-bottom: 2rem;
        }

        h1 {
            margin: 0;
        }

        .movie-detail {
            display: flex;
            flex-direction: column;
            margin: 0 25px;
            width: 50%;
            flex-wrap: wrap;

            .tag-name {
                margin: 0;
                margin-top: 0.5rem;
                color: #bdbdbd;
            }

            .genre {
                display: flex;
                flex-wrap: wrap;

                &>div {
                    margin-right: 13px;
                    border: 1px solid #608dda;
                    padding: 1px 8px;
                    border-radius: 5px;
                    font-size: .9rem;
                    text-align: center;
                    margin-bottom: 15px;
                }
            }

            .language {
                display: flex;
                margin-top: 0;
                margin-bottom: 2rem;
            }

            .run-time {
                font-size: 0.9rem;
                margin-top: 1rem;
            }

            .ratings {
                font-size: 0.9rem;

                i {
                    margin-right: 11px;
                    margin-bottom: 10px;
                }

                .fas.fa-heart {
                    color: #a70000;
                    font-size: 18px;
                }

                .fab.fa-imdb {
                    font-size: 23px;
                    color: #eab000;
                }
            }
        }
    }

    .ytube-title {
        margin-top: 5rem;
        text-align: center;
    }

    .ytube {
        position: relative;

        @media(max-width: 680px){
            img{
                width: 95% !important;
            }
        }
        
        img {
            border: 5px solid #3f6098;
            max-width: 100%;
            width: 600px;
            height: auto;
            margin: 0 auto;
            display: block;
            filter: brightness(65%);
            transition: all 0.2s linear;
        }

        .fas.fa-play-circle {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            font-size: 4rem;
            color: #ffffff;
            cursor: pointer;
            z-index: 98;
        }

        img:hover,
        .fas.fa-play-circle:hover~img {
            filter: brightness(50%);
        }
    }

    @media(max-width: 768px) {
        .m-details{
            .synopsis-cast{
                flex-direction: column;
            }
        }   
    }

    @media(max-width: 500px) {
        .m-container {
            margin-left: 0;
            margin-right: 0;
            margin-top: 3rem;
            justify-content: center;

            h1 {
                margin-top: 1rem;
            }

            .movie-detail {
                width: 100% !important;
                margin-top: 2.5rem;
                margin: 0 !important;
            }
        }

        .m-details {
            margin-left: 0;
            margin-right: 0;
        }
    }
}



.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }
  
  .modal-main {
    position:fixed;
    background: white;
    width: 80%;
    height: auto;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }