.loaderRoot{
    position: relative;
    width: 100%;
}

.showbox {
    position: absolute;
    top: 1rem;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 5%;
}


.loader {
    position: relative;
    margin: 0px auto;
    width: 50px;
}

.loader:before {
    content: '';
    display: block;
    padding-top: 100%;
}

.circular {
    animation: rotate 2s linear infinite;
    height: 100%;
    transform-origin: center center;
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
}


.path {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
    animation:
        dash 1.5s ease-in-out infinite,
        color 6s ease-in-out infinite;
    stroke-linecap: round;
}

@keyframes rotate {
    100% {
        transform: rotate(360deg);
    }
}

@keyframes dash {
    0% {
        stroke-dasharray: 1, 200;
        stroke-dashoffset: 0;
    }

    50% {
        stroke-dasharray: 89, 200;
        stroke-dashoffset: -35;
    }

    100% {
        stroke-dasharray: 89, 200;
        stroke-dashoffset: -124;
    }
}

@keyframes color {

    100%,
    0% {
        stroke: rgb(160, 241, 253);
    }

    40% {
        stroke: rgb(0, 204, 255);
    }

    66% {
        stroke: rgb(66, 176, 250);
    }

    80%,
    90% {
        stroke: rgb(164, 238, 255);
    }
}
/* End loader */


.filter-section {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    select,
    input[type='number'],
    .year-input {
      border: none;
      width: 150px;
      border-radius: 5px;
      padding: 0 0.5rem;
      height: 35px;
      background: #2e4469;
      color: #d8d8d8;
    }

    .year-input::placeholder,
    .input-icon,
    .year-input {
      color: #d8d8d8;
      cursor: pointer;
    }

    // input::placeholder {
    //   color: #919191;
    // }

    .section-container,
    form {
      margin: 0 1rem;
    }

    .section-container {
      display: flex;
      flex-direction: column;
      margin-bottom: 2rem;

      label {
        margin-bottom: 5px;
        color: #a2a2a2;
      }

      .year-picker {
        z-index: 99;
        width: auto !important;
        border: none !important;
        background: none !important;

        .picker-panel {
          height: 180px !important;
        }
      }
    }

    .adult label {
      text-align: center;
    }

    // Toggle button
    .switch {
      position: relative;
      display: inline-block;
      width: 50px;
      height: 30px;
      margin: 0;
    }

    .switch input {
      display: none;
    }

    .slider {
      position: absolute;
      cursor: pointer;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: #4f658a;
      transition: 0.4s;
    }

    .slider::before {
      position: absolute;
      content: "";
      height: 21px;
      width: 21px;
      left: 5px;
      bottom: 5px;
      background-color: #41b1e1d4;
      transition: 0.4s;
    }

    input:checked+.slider {
      background-color: #303f58;
    }

    input:checked+.slider::before {
      transform: translateX(18px);
    }

    .slider.round {
      border-radius: 34px;
    }

    .slider.round::before {
      border-radius: 50%;
    }

    // End Toggle button

  }